import React from 'react';
var NavLink = require('react-router-dom').NavLink;
var Menu = require('react-burger-menu').slide;
var menuStyles = {
    bmBurgerButton: {
      position: 'fixed',
      width: '36px',
      height: '30px',
      right: '30px',
      top: '36px'
    },
    bmBurgerBars: {
      background: '#FFC30E'
    },
    bmCrossButton: {
      height: '24px',
      width: '24px'
    },
    bmCross: {
      background: '#FFC30E'
    },
    bmMenu: {
      background: '#00467F',
      padding: '2.5em 1.5em 0',
      fontSize: '1.15em',
      color: '#FFC30E'
    },
    bmMorphShape: {
      fill: '#373a47'
    },
    bmItemList: {
      color: '#FFC30E',
      padding: '0.8em'
    },
    bmItem: {
      display: 'block'
    },
    bmOverlay: {
      background: 'rgba(0, 0, 0, 0.3)'
    }
  
}

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
  return images;
}
const images = importAll(require.context('../assets/images', false, /\.(png|jpe?g|svg)$/));

function Footer () {
  return (
    <ul className='titleHeader'>
    
    <li className='nonBurgerNav'>
            <ul className='nav'>
              <li>
                <NavLink exact activeClassName='active' to='/'>&nbsp; Home</NavLink>
              </li>
              <li>
                <NavLink activeClassName='active' to='/about'>About</NavLink>
              </li>
              <li>
                <NavLink activeClassName='active' to='/calendar'>Calendar</NavLink>
              </li>
              <li>
                <NavLink activeClassName='active' to='/store'>Store</NavLink>
              </li>
            </ul>
    </li>
    <li className='burgerNav'>
      <p>
        American Legion Post 460 - <em onClick={()=>{
          document.body.scrollTop = 0; // For Safari
          document.documentElement.scrollTop = 0;}}>Back to Top ↑</em>
      </p>
    </li>
    </ul>
  )
}

export default Footer;